import './Index.css'

function enviarFormulario() {
    const nombre = document.getElementById('nombre').value;
    const apellido = document.getElementById('apellido').value;
    const gmail = document.getElementById('gmail').value;
    const opinion = document.getElementById('opinion').value;

    if (!nombre || !apellido || !gmail || !opinion) {
        alert('Todos los campos son obligatorios.');
        return;
    }

    const resultado = `
        <p>Nombre: ${nombre}</p>
        <p>Apellido: ${apellido}</p>
        <p>Gmail: ${gmail}</p>
        <p>Opinión: ${opinion}</p>
    `;

    document.getElementById('resultado').innerHTML = resultado;
}

export function Index() {
    return (
        <div className="contacto">
            <h1>Contacto</h1>
            <div className="container">
                <h2>Formulario de Opinión</h2>
                <form id="opinionForm">
                    <label htmlFor="nombre">Nombre:</label>
                    <input type="text" id="nombre" name="nombre" required />
        
                    <label htmlFor="apellido">Apellido:</label>
                    <input type="text" id="apellido" name="apellido" required />

                    <label htmlFor="gmail">Gmail:</label>
                    <input type="text" id="gmail" name="gmail" required />
        
                    <label htmlFor="opinion">Opinión:</label>
                    <textarea id="opinion" name="opinion" rows="5" required></textarea>
        
                    <button type="button" onClick={enviarFormulario}>Enviar Opinión</button>
                </form>
        
                <div id="resultado" style={{ marginTop: '20px' }}></div>
            </div>
        </div>
    )
}
