import './App.css';


import { Navbar } from './componentes/Navbar';

import { Login } from './componentes/Login';

import { Index as Inicio } from './componentes/INICIO/Index';
import { Index as Alta } from './componentes/ALTA/Index';
import { Index as Carrito } from './componentes/CARRITO/Index';
import { Index as Contacto } from './componentes/CONTACTO/Index';
import { Index as Nosotros } from './componentes/NOSOTROS/Index';
import { Index as Otra } from './componentes/OTRA/Index';

import { Route, Routes, useNavigate } from 'react-router';
import { NavLink } from 'react-router-dom';
import { useDispatch, useSelector } from 'react-redux';
import { accionSetLogin, accionSetUsuarioLogueado } from './state/actions';


function mostrarCarrito() {
  const carrito = JSON.parse(localStorage.getItem('carrito')) || [];
  const popup = document.createElement('div');
  popup.className = 'carrito-popup';

  const closeButton = document.createElement('span');
  closeButton.className = 'close-button';
  closeButton.innerHTML = '&times;';
  closeButton.onclick = () => popup.remove();

  const contenido = carrito.map(producto => `
      <div class="carrito-item">
          <img src="${producto.foto}" alt="foto de ${producto.nombre}" class="carrito-item-foto" />
          <div class="carrito-item-info">
              <p><b>Producto:</b> ${producto.nombre}</p>
              <p><b>Cantidad:</b> ${producto.cantidad}</p>
              <p><b>Precio:</b> $${producto.precio}</p>
          </div>
      </div>
      <hr />
  `).join('');

  popup.innerHTML = `<div class="carrito-content">${contenido}</div>`;
  popup.appendChild(closeButton);
  document.body.appendChild(popup);

  document.addEventListener('keydown', function(event) {
      if (event.key === 'Escape') {
          popup.remove();
      }
  });
}

function App() {
  const cantidad = useSelector(state => state.cantidad)
  const login = useSelector(state => state.login)
  const usuario = useSelector(state => state.usuarioLogueado)

  const dispatch = useDispatch()
  const navigate = useNavigate()

  function logout() {
    dispatch(accionSetUsuarioLogueado({}))
    dispatch(accionSetLogin(false))

    navigate('/')
  }

  const { admin } = usuario

  return (
    <div className="App">
      {login
        ? <header>
          <Navbar admin={admin} />
          <div>
            <div id="logo">Logo</div>
            <div id="barra-busqueda">
              <form action="#">
                <label htmlFor="Buscar">Buscar</label>
                <input type="text" />
                <input type="submit" value="Buscar" />
              </form>
            </div>
            { !admin && 
            <div id="boton-carrito" onClick={mostrarCarrito}>
            <div className="position-relative">
                <NavLink className="nav-link" to="/carrito">C</NavLink>
                <span className="position-absolute top-0 start-50 translate-middle badge rounded-pill bg-danger">
                  {cantidad > 0 && cantidad}
                  </span>
            </div>
          </div>
        }
        <div id="boton-logout" onClick={logout}><b>Logout</b> <i>{usuario.nombre}</i></div>
      </div>
    </header>
    :
    <header>
      <span id="titulo">ECommerce</span>
    </header>
    }

    <main>
      {login
        ? <Routes>
          <Route index element={<Inicio />} />

          <Route path="inicio" element={<Inicio />} />
          { admin && <Route path="alta" element={<Alta />} /> }
          { !admin && <Route path="carrito" element={<Carrito />} /> }
          <Route path="contacto" element={<Contacto />} />
          <Route path="nosotros" element={<Nosotros />} />
          <Route path="otra" element={<Otra />} />

          <Route path="*" element={<Inicio />} />
        </Routes>
        : <Login />
      }
    </main>

    <footer>
      <h3>Copyright 2024</h3>
    </footer>
  </div>
  );
}

export default App;


