import './ObtenerFoto.css'

import servicioUpload from '../../servicios/upload'
import { useEffect, useState } from 'react'

export function ObtenerFoto({ escribirCampoFoto }) {
    const [ porcentaje, setPorcentaje ] = useState(0)
    const [ urlFoto, setUrlFoto ] = useState('')

    useEffect(() => {
        if (urlFoto) {
            escribirCampoFoto(urlFoto);
            setPorcentaje(0);
        }
    }, [escribirCampoFoto, urlFoto]);

    const dragEnter = e => {
        console.log('dragEnter')
        e.preventDefault()
    }
    const dragLeave = e => {
        console.log('dragLeave')
        e.preventDefault()
    }
    const dragOver = e => {
        console.log('dragOver')
        e.preventDefault()
    }
    const drop = e => {
        console.log('drop')
        e.preventDefault()

        const archivo = e.dataTransfer.files[0]
        enviarFoto(archivo)
    }

    const enviarFoto = archivo => {
        console.log(archivo)

        if(archivo.type.includes('image')) {
            const data = new FormData()
            data.append('archivo', archivo)
            servicioUpload.enviarFormDataAjax(data, porcentaje => {
                setPorcentaje(porcentaje)
            }, urlFoto => {
                console.log(urlFoto)
                setUrlFoto(urlFoto)
                escribirCampoFoto(urlFoto)
            })
        }
        else {
            console.error('El archivo no es una imagen')
        }
    }

    const change = e => {
        const archivo = e.target.files[0]
        console.log(archivo)
        enviarFoto(archivo)
    }

    return (
        <div className="ObtenerFoto">
            <input type="file" id="archivo" onChange={change} />
            <div
                id="drop"
                onDragEnter={dragEnter}
                onDragLeave={dragLeave}
                onDragOver={dragOver}
                onDrop={drop}
            >
                { porcentaje > 0 && <><progress min="0" max="100" value={porcentaje}></progress> <span clasName='info'>{porcentaje}%</span></>}
                <label htmlFor='archivo'>
                { porcentaje > 0
                    ? (urlFoto? <img src={urlFoto} alt="" /> : '')
                    : 'D&D or Click'
                }
                </label>

            </div>

        </div>
    )
}